/*
 * Export: 
 * */
import $ from './jTool';
import Core from './Core';
import Cache from './Cache';
const Export = {
	html: function () {
		const html = '<a href="" download="" id="gm-export-action"></a>';
		return html;
	}
	/*
	 *  .xls
	 * @param $table:grid,
	 * @param fileName: 
	 * @param onlyChecked: 
	 * */
	,__exportGridToXls: function($table, fileName, onlyChecked){
		let Settings = Cache.getSettings($table);
		const gmExportAction = $('#gm-export-action'); //createDOM
		if(gmExportAction.length === 0){
			Core.outLog(':supportExport', 'error');
			return false;
		}
		// type base64
		const uri = 'data:application/vnd.ms-excel;base64,';

		//thead
		let	theadHTML = '';
		//tbody
		let	tbodyHTML = '';

		const thDOM = $('thead[grid-manager-thead] th[th-visible="visible"][gm-create="false"]', $table);

		let	trDOM,
			tdDOM;
		//
		if(onlyChecked){
			trDOM = $('tbody tr[checked="true"]', $table);
		}else{
			trDOM = $('tbody tr', $table);
		}
		$.each(thDOM, function(i, v){
			theadHTML += `<th>${v.getElementsByClassName('th-text')[0].textContent}</th>`;
		});
		$.each(trDOM, function(i, v){
			tdDOM = $('td[gm-create="false"][td-visible="visible"]', v);
			tbodyHTML += '<tr>';
			$.each(tdDOM, function(i2, v2){
				tbodyHTML += v2.outerHTML;
			});
			tbodyHTML += '</tr>';
		});
		// html
		const exportHTML =`
			<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40">
				<head><meta http-equiv="Content-Type" content="text/html; charset=utf-8"></head>
				<body>
					<table>
						<thead>'
							${theadHTML}
						</thead>
						<tbody>
							${tbodyHTML}
						</tbody>
					</table>
				</body>
			</html>`;
		gmExportAction.prop('href', uri + base64(exportHTML));
		gmExportAction.prop('download', (fileName || Settings.gridManagerName) +'.xls');
		gmExportAction.get(0).click();

		function base64(s) {
			return window.btoa(unescape(encodeURIComponent(s)))
		}

		// true
		return true;
	}
};
export default Export;



