import $ from './jTool';
import Cache from './Cache';
import Base from './Base';
import Sort from './Sort';
import Export from './Export';
import Core from './Core';
const PublishMethod= {
	/*
	 * jToolGridManager
	 * @param $table: table [jTool Object]
	 * */
	get: function($table) {
		return Cache.__getGridManager($table);
	}
	/*
	 * 
	 * ,
	 * @param $table: table [jTool Object]
	 * */
	,getLocalStorage: function($table){
		return Cache.getUserMemory($table);
	}
	/*
	 * 
	 * @param $table: table [jTool Object]
	 * return 
	 * */
	,clear:function($table){
		return Cache.delUserMemory($table);
	}
	/*
	 * @
	 * @param $table: table [jTool Object]
	 * @param target: tr[Element or NodeList]
	 * */
	,getRowData: function ($table, target) {
		return Cache.__getRowData($table, target);
	}
	/*
	* 
	* @param sortJson: json :{th-name:'down'} valuesortUpText  sortDownText
	* @param callback: [function]
	* @param refresh: [boolean, true]
	* */
	,setSort: function($table, sortJson, callback, refresh){
		Sort.__setSort($table, sortJson, callback, refresh)
	}
	/*
	* ThTD
	* @param $table: table [jTool Object]
	* @param target: th[Element or NodeList]
	* */
	,showTh: function($table, target){
		Base.setAreVisible($(target), true);
	}
	/*
	* ThTD
	 * @param $table: table [jTool Object]
	 * @param target: th[Element or NodeList]
	* */
	,hideTh: function($table, target){
		Base.setAreVisible($(target), false);
	}
	/*
	*  .xls
	* @param $table:grid,
	* @param fileName: 
	* @param onlyChecked: 
	* */
	,exportGridToXls: function($table, fileName, onlyChecked){
		return Export.__exportGridToXls($table, fileName, onlyChecked);
	}
	/**
	 * 
	 * @param $table: table [jTool object]
	 * @param query:  [Object]
	 * @param callback: 
	 * @param isGotoFirstPage: [Boolean default=true]
	 * :
	 * - querykey, query.
	 * - setQuery() 
	 * - query, pagingAfter(query)
	 * - setQueryquery, setQueryquery
	 */
	,setQuery: function($table, query, isGotoFirstPage, callback){
		const settings = Cache.getSettings($table);
		if(typeof(isGotoFirstPage) !== 'boolean'){
			callback = isGotoFirstPage;
			isGotoFirstPage = true;
		}
		$.extend(settings, {query: query});
		if(isGotoFirstPage){
			settings.pageData.cPage = 1;
		}
		Cache.updateSettings($table, settings);
		Core.__refreshGrid($table, callback);
	}
	/**
	 * ajaxData
	 * @param $table: table [jTool object]
	 * @param ajaxData: 
	 */
	,setAjaxData: function ($table, ajaxData) {
		const settings = Cache.getSettings($table);
		$.extend(settings, {ajax_data: ajaxData});
		Cache.updateSettings($table, settings);
		Core.__refreshGrid($table);
	}
	/*
	*  
	* @param $table:grid,
	* @param isGotoFirstPage:  [boolean, false]
	* @param callback: 
	* */
	,refreshGrid: function($table, isGotoFirstPage, callback){
		const settings = Cache.getSettings($table);
		if(typeof(isGotoFirstPage) !== 'boolean'){
			callback = isGotoFirstPage;
			isGotoFirstPage = false;
		}
		if(isGotoFirstPage){
			settings.pageData['cPage'] = 1;
			Cache.updateSettings($table, settings);
		}
		Core.__refreshGrid($table, callback);
	}
	/*
	* 
	* @param $table: table [jTool Object]
	* return  [NodeList]
	* */
	,getCheckedTr: function($table) {
		return $table.get(0).querySelectorAll('tbody tr[checked="true"]');
	}
	/*
	* 
	* @param $table: table [jTool Object]
	* */
	,getCheckedData: function($table){
		return Cache.__getRowData($table, this.getCheckedTr($table))
	}
};

/*
	//
	'init',					// 
	'setSort',				// 
	'get',					// jToolGridManager
	'showTh',				// ThTD
	'hideTh',				// ThTD
	'exportGridToXls',		//  .xls
	'getLocalStorage',		// 
	'setQuery',				// query pagingAfter(query)
	'setAjaxData',          // ajax_data, 
	'refreshGrid',			//  
	'getCheckedTr',			// 
	'getRowData',			// 
	'getCheckedData',		// 
	'clear'					// 
*/
// 
const publishMethodArray = ['init'];
for(let key in PublishMethod){
	publishMethodArray.push(key);
}
export {PublishMethod, publishMethodArray};



